:root {
  --bg: #f7f7f8;
  --card: #fff;
  --accent: #333;
  --muted: #666;
}

body {
  background: var(--bg);
  color: var(--accent);
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial;
  display: flex;
}

.app {
  background: var(--card);
  text-align: center;
  border-radius: 8px;
  width: 360px;
  padding: 24px;
  box-shadow: 0 6px 18px #0000000f;
}

.mode-selector {
  justify-content: center;
  gap: 12px;
  margin-bottom: 12px;
  display: flex;
}

.status {
  margin-bottom: 12px;
  font-weight: 600;
}

.board {
  user-select: none;
  grid-template-columns: repeat(3, 1fr);
  gap: 8px;
  display: grid;
}

.square {
  cursor: pointer;
  background: #fff;
  border: 2px solid #ddd;
  width: 100px;
  height: 100px;
  font-size: 36px;
}

.square:disabled {
  cursor: default;
  color: #333;
}

.controls {
  margin-top: 12px;
}

.controls button {
  padding: 8px 12px;
}

.hint {
  color: var(--muted);
  margin-top: 8px;
  font-size: 12px;
}
